// Programming.tsx

import React, { FC, useState } from 'react';
import { Navigator } from '../Navigator';
import ContentFrame from '../ContentFrame';
import { useParams, useNavigate } from 'react-router-dom';

const strTitleText = "";
const LessonPrefix = 'programming';

  // 
const LessonIndex = [ 'TOP',
                      'Processing', 
                      '',
                      '',             // 
                      '',       // 
                      '',
                      '',         // 
                      '',
                      '',
                      '',              // 
                      '',
                      '',
                      '',
                      ''];

const Programming: FC = () => {
  const { lessonId } = useParams();
  const navigate = useNavigate(); // useNavigate

  const handleLinkClick = (linkIndex: number) => {
    // 

    console.log(`Link clicked in Programming: ${linkIndex}`);

    // URL
    navigate(`/programming/${linkIndex}`);
  };

  let lessonNumber = 0;
  console.log(`lessonId in Programming: ${lessonId}`);

  if (lessonId !== undefined) {

    // lessonId 
    lessonNumber = parseInt(lessonId, 10);

    // 
    if (isNaN(lessonNumber)) {
    }
    return (
      <div className="Programming">

        {/*  */}
        <div className="main-content">
          <ContentFrame LessonPrefix={LessonPrefix} LessonIndex={lessonNumber} />
        </div>

        {/* Navigator */}
        <Navigator onClicked={handleLinkClick} LessonIndex={LessonIndex} TitleText={strTitleText} lessonId={lessonNumber} />
      </div>
    );
  }
  // lessonId 
  return (
    <div className="Programming">

      {/*  */}
      <div className="main-content">
        <ContentFrame LessonPrefix={LessonPrefix} LessonIndex={0} />
      </div>

      {/* Navigator */}
      <Navigator onClicked={handleLinkClick} LessonIndex={LessonIndex} TitleText={strTitleText} lessonId={lessonNumber} />
    </div>
  );
};

export default Programming;
