// ContentFrame.tsx

import React from 'react';
import './ContentFrame.css';

interface ContentFrameProps {
  LessonPrefix: string;
  LessonIndex: number;
}

const ContentFrame: React.FC<ContentFrameProps> = ({ LessonPrefix, LessonIndex }) => {
  // LessonIndex 2
  const formattedLessonIndex = LessonIndex.toString().padStart(2, '0');

  // 
  const componentName = `${LessonPrefix}-${formattedLessonIndex}`;

  // 
  const DynamicComponent = require(`./programming/${componentName}`).default;

  return (
    <div className="content-frame">
        <DynamicComponent />
    </div>
  );
};

export default ContentFrame;
